<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="HR" SubModuleCode="" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="RESIGNATION_ID" :listType="['List']" :ListData="ResignationData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'RESIG_FROM'">
                    <td class="text-center single-action tourguide_a">
                        {{ $options.filters.formatDate(data.row.RESIG_FROM) }}
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'date_of_leave'">
                    <td class="text-center single-action tourguide_a">
                        {{ $options.filters.formatDate(data.row.date_of_leave) }}
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'status_code'">
                    <td class="text-center single-action tourguide_a">
                      <span v-if="data.row.status_code=='Resignation_Accepted'">{{ $t('Accepted') }} </span>
                      <span v-if="data.row.status_code=='Resignation_Rejected'">{{ $t('ApplicantReject') }} </span>
                      <span v-if="data.row.status_code=='Resignation_Relieved'">{{ $t('Relieved') }} </span>
                      <span v-if="data.row.status_code=='Resignation_Pending'">{{ $t('EVENT_INV_PENDING') }} </span>
                    </td>
                </template>
            </template>
        </tg-list>
        <AddEmployee v-if="ShowAddEmployee"></AddEmployee>
        <modalpreview v-if="showpreviewModal" :ResignationReason="ResignationReason"></modalpreview>
        <modalstatusHistory v-if="showHistoryModal" :ResignationId="ResignationId"></modalstatusHistory>
        <modalAcceptRequest v-if="showAcceptRequestModal" :ResignationItem="ResignationItem"></modalAcceptRequest>
    </section>
</template>
<script>
import Schema from "./ResignationSchema"
import moment from 'moment'
import modalpreview from './previewmodal.vue'
import modalstatusHistory from './modalstatusHistory.vue'
import modalAcceptRequest from './modalAcceptRequest.vue'
import DataServices from "@/services/DataService";
import AddEmployee from './AddEmployee.vue';
export default {
    components: { modalpreview, modalstatusHistory, modalAcceptRequest, AddEmployee },
    data() {
        return {
            isLoading: false,
            ShowAddVendor: false,
            showpreviewModal: false,
            ResignationId:0,
            showHistoryModal:false,
            showAcceptRequestModal:false,
            Headers: [{
                "COLUMN_NAME": "NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Employee Name',
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "email_id",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Email Id',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "DEPT_NAME",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Department Name',
                "DISPLAY_ORDER": 3,
            },
            {
                "COLUMN_NAME": "DESIG_NAME",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Designation Name',
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "date_of_leave",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Relieving Date',
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "RESIG_FROM",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Effective Resignation Date Request',
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "status_code",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Status',
                "DISPLAY_ORDER": 5,
            },

            ],
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            SortBy: '',
            ResignationReason:'',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            ResignationData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            HeaderText: '',
            UserType:'',
            IsDefault:'',
            ResignationItem:[]
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
        vm.UserType = vm.GetUserInfo.UserType;
        vm.IsDefault=vm.GetUserInfo.IsDefault;
        if(vm.UserType=='CA' && vm.IsDefault){
            vm.listheaderbuttons[0].isdisabled = true;
        }
    },
    methods: {
        AddEmployee: function (EditId) {
            this.EditId = EditId;
            this.ShowAddEmployee = true;
            this.$router.push({ name: 'AddEmployee', params: { reqfrom: 'ExitPolicy/AddEmployee', id: EditId} });
        },
        formatDate: function (value) {
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddEmployee":
                    this.AddEmployee(0, 'AddEmployee');
                break;
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Reason':
                        action.isVisible = true;
                        break;
                     case 'Resource Impact':
                        action.isVisible = true;
                        break;
                     case 'Status History':
                        action.isVisible = true;
                        break;
                     case 'Accept':
                        action.isVisible = true;
                        break;
                        case 'Reject':
                        action.isVisible = true;
                        break;
                        case 'Relieve':
                        action.isVisible = true;
                        break;
                        case 'Feedback Questions':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {  
            switch (callbackfunction) {
                case "Reason":
                    this.OpenReasonModal(item);
                    break;
                case "Statushistory":
                    this.Statushistory(item);
                    break;
                case "Accept":
                    this.AcceptRequest(item);
                    break;
                case "MakeOffer":
                    this.MakeOffer();
                    break;
                case "ViewonAction":
                    this.ViewCandidateDetail();
                    break;                    
            }
        },
        OpenReasonModal: function(item){
            var vm=this;
            vm.ResignationReason= item.RESIGNATION_REASON;
            vm.showpreviewModal = true;
        },
        Statushistory: function(item){
            var vm=this;
            vm.ResignationId= item.RESIGNATION_ID;
            vm.showHistoryModal = true;
        },
        AcceptRequest: function(item){
            var vm=this;
            vm.ResignationItem= item;
            vm.showAcceptRequestModal = true;
        },
        FetchData: async function () {
            var vm = this;
            var url = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`
            if (!vm.isBlank(this.searchCondition)) {
                url += `&search=${this.searchCondition}`;
            }
            await DataServices.GetEmployeeResignationListing(url).then((response) => {
                debugger;
                if (response.data && response.data.length > 0) {
                    vm.ResignationData = [];
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "RESIG_FROM") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "date_of_leave") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.COLUMN_NAME == "status_code") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vm.ResignationData = response.data;
                    vm.TotalRecords = response.data[0].TOTAL_RECORD;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;
                } else {
                    vm.ResignationData = [];
                    vm.TotalRecords = 0
                    vm.noRecords = true;
                }
            })
        }
    }
}
</script>
