<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AcceptRequest') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import moment from 'moment'
export default {
    props: {
        ResignationItem: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('EmployeeResignationFromRequest'),
                            name: "FromRequest",
                            value: "",
                            disabled:true,
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ResignationExpectedDate'),
                            name: "ExpectedDate",
                            value: "",
                            disabled:true,
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EffectiveResignationFrom'),
                            name: "ResignationFrom",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Reason'),
                            name: "Reason",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vm = this;
                (vm.FormSchema)[0].Data[0].value =vm.$options.filters.formatDate(vm.ResignationItem.RESIG_FROM);
                vm.expecteddate(vm.ResignationItem);
        },
        formatDate: function (value) {
            if (value) {
                return moment(value, "YYYY-MM-DD").format(String('MM/DD/YYYY'))
            }
        },
        expecteddate:function (item) {
        var vm=this;
        var url = `regid=${item.RESIGNATION_ID}&userId=${item.user_id}`;
            DataServices.GetExpectedReleaveDate(url).then(
             function (response) {
                (vm.FormSchema)[0].Data[1].value=vm.$options.filters.formatDate(response.data[0].result);
             });
        },
        Close: function () {
            this.$parent.showAcceptRequestModal = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                ResignationId: vm.ResignationItem.RESIGNATION_ID == null ? 0 : vm.ResignationItem.RESIGNATION_ID,
                EffectiveFrom: vm.$options.filters.formatDate(formfields.ResignationFrom),
                Reason: formfields.Reason,
                Status: "Resignation_Accepted"
            };
            var params = JSON.stringify(jsonobj);
            DataServices.ResignationStatus(params).then((response) => {
                if (response.data.msg== 'success') {
                    vm.ShowAlert(vm.$t("AcceptedSuccess", [vm.$t("Resignation")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resignation")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>