<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('StatusHistory') }}</h4>
                        <button type="button" class="close" v-on:click="close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <div class="col-md-12 p-0 mt-0">
                    <div class="row">
                        <div class="listing mt-2">
                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered dt-responsive nowrap mt-0" id="tblProjectList1">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th><span href="javascript:;">{{ $t('Name') }}</span></th>
                                            <th><span href="javascript:;">{{ $t('Status') }} </span></th>
                                            <th><span href="javascript:;">{{ $t('Reason') }} </span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-if="noRecord">
                                                <td colspan="7" class="text-center text-danger">
                                                    {{$t('NoRecordfound')}}
                                                </td>
                                            </tr>                                         
                                            <tr v-for="(item,index) in statusHistoryData" :key="index">
                                                <td>{{item.Name}}</td>
                                                <td>{{item.status_name}}</td>
                                                <td>{{item.reason}}</td>
                                            </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ResignationId: {
            type: String,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            CompanyName: '',
            SortBy: '',
            SortExp: '',
            statusHistoryData:[],
            noRecord: false,
            PageSize: "10",
            PageNumber: "1",
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyName = this.GetUserInfo.CompanyName;
        vm.FetchData();
    },
      
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `regid=${vm.ResignationId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`
            await DataServices.GetStatusHistoryListing(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.statusHistoryData = [];
                    vm.statusHistoryData = response.data;
                    vm.TotalRecords = response.data[0].TOTAL_RECORD;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;
                } else {
                    vm.statusHistoryData = [];
                    vm.TotalRecords = 0
                    vm.noRecords = true;
                }
            })
        },
        close: function () {
            this.$parent.showHistoryModal = false;
        },
    }
}
</script>